import React from 'react';
import { useNavigate } from 'react-router';
import Button from '../../../Button/Button';
import Textbox from '../../../Textbox/Textbox';
import './newdevice.scss';

const NewDevice: React.FC = () => {
  const navigate = useNavigate();
  return (
    <div className="app__layout-device__new-device">
      <div className="app__layout-device__new-device__title">Qun l thit b</div>
      <div className="app__layout-device__new-device__container">
        <div className="row form-label">Thng tin thit b</div>
        <div className="row"><Textbox label="M thit b *" /><Textbox label="Loi thit b *" /></div>
        <div className="row"><Textbox label="Tn thit b *" /><Textbox label="Tn ng nhp *" /></div>
        <div className="row"><Textbox label="a ch IP *" /><Textbox label="Mt khu *" /></div>
        <div className="row"><Textbox label="Dch v s dng *" /></div>
        <div className="row note">* L trng thng tin bt buc</div>
      </div>
      <div className="app__layout-device__new-device__btn__bottom">
        <Button
          text='Hy b'
          isOutlined
          onClick={() => navigate('/dashboard/device/list')}
        />
        <Button
          text='Thm thit b'
          onClick={() => navigate('/dashboard/device/list')}
        />
      </div>
    </div>
  );
};

export default NewDevice;
