import React from 'react';
import { useNavigate } from 'react-router';
import './detaildevice.scss';
import { ReactComponent as AddIc } from '../../../../Assets/add-square.svg';

const DetailDevice: React.FC = () => {
  const navigate = useNavigate();
  return (
    <div className="app__device-layout__detail-device">
      <div className="app__device-layout__detail-device__title">
        Qun l thit b
      </div>
      <div className="app__device-layout__detail-device__container">
        <div className="row form-label">Thng tin thit b</div>
        <div className="row">
          <div className="flex-1">M thit b:</div>
          <div className="flex-1">Loi thit b:</div>
        </div>
        <div className="row">
          <div className="flex-1">Tn thit b:</div>
          <div className="flex-1">Tn ng nhp:</div>
        </div>
        <div className="row">
          <div className="flex-1">a ch IP:</div>
          <div className="flex-1">Mt khu:</div>
        </div>
        <div className="row">
          <div className="flex-1">Dch v s dng:</div>
        </div>
      </div>
      <div
        className="app__device-layout__detail-device__float-btn"
        onClick={() => navigate('/dashboard/device/modify')}
      >
        <span>
          <AddIc />
        </span>
        <span>Quay li</span>
      </div>
    </div>
  );
};
//conmeo

export default DetailDevice;
