import React from 'react';
import { useNavigate } from 'react-router';
import Button from '../../../Button/Button';
import Select from '../../../Select/Select';
import Textbox from '../../../Textbox/Textbox';
import './modifydevice.scss';

const typeofDevice = ['Kiosk', 'Display counter'];
const ModifyDevice: React.FC = () => {
  const navigate = useNavigate();
  return (
    <div className='app__layout-device__modify-device'>
      <div className='app__layout-device__modify-device__title'>
        Qun l thit b
      </div>
      <div className='app__layout-device__modify-device__container'>
        <div className='row form-label'>Thng tin thit b</div>
        <div className='row'>
          <Textbox label='M thit b *' />
          <Select label='Loi thit b' options={typeofDevice} />
        </div>
        <div className='row'>
          <Textbox label='Tn thit b *' />
          <Textbox label='Tn ng nhp *' />
        </div>
        <div className='row'>
          <Textbox label='a ch IP *' />
          <Textbox label='Mt khu *' />
        </div>
        <div className='row'>
          <Textbox label='Dch v s dng *' />
        </div>

        <div className='row note'>* L trng thng tin bt buc</div>
      </div>
      <div className='app__layout-device__modify-device__btn__bottom'>
        <Button
          text='Hy b'
          isOutlined
          onClick={() => navigate('/dashboard/device/modify')}
        />
        <Button
          text='Cp nht thit b'
          onClick={() => navigate('/dashboard/device/modify')}
        />
      </div>
    </div>
  );
};

export default ModifyDevice;
