import React from 'react';
import { useNavigate } from 'react-router';
import Button from '../../../Button/Button';
import Select from '../../../Select/Select';
import Textbox from '../../../Textbox/Textbox';
import './newaccount.scss';

const typeofDevice = ['Kiosk', 'Display counter'];
const NewAccount: React.FC = () => {
  const navigate = useNavigate();
  return (
    <div className='app__layout-system__new-account'>
      <div className='app__layout-system__new-account__title'>Qun l tai khoan</div>
      <div className='app__layout-system__new-account__container'>
        <div className='row form-label'>Thng tin tai khoan</div>
        <div className='row'>
          <Textbox label='Ho tn *' />
          <Textbox label='Tn ng nhp *' />
        </div>
        <div className='row'>
          <Textbox label='S in thoai *' />
          <Textbox label='Mt khu *' />
        </div>
        <div className='row'>
          <Textbox label='Email *' />
          <Textbox label='Nhp lai mt khu *' />
        </div>
        <div className='row'>
          <Select label='Vai tro *' options={typeofDevice} />
          <Select label='Tinh trang *' options={typeofDevice} />
        </div>
        <div className='row note'>* L trng thng tin bt buc</div>
      </div>
      <div className='app__layout-system__new-account__btn__bottom'>
        <Button
          text='Hy b'
          isOutlined
          onClick={() => navigate('/dashboard/system/accounts')}
        />
        <Button
          text='Thm thit b'
          onClick={() => navigate('/dashboard/system/accounts')}
        />
      </div>
    </div>
  );
};

export default NewAccount;
