import React from 'react';
import { useNavigate } from 'react-router';
import './newrole.scss';
import { ReactComponent as AddIc } from '../../../../Assets/add-square.svg';
import Textbox, { InputType } from '../../../Textbox/Textbox';
import Button from '../../../Button/Button';

const permisionGroup = ["Tt c", "Nhm chc nng A", "Nhm chc nng B", "Nhm chc nng C"]

const NewRole: React.FC = () => {
  const navigate = useNavigate();
  return (
    <div className="app__layout-system__new-role">
      <div className="app__layout-system__new-role__title">
        Danh sch vai tr
      </div>
      <div className="app__layout-system__new-role__container">
        <div className="row form-label">Thng tin vai tr</div>
        <div className="row full">
          <div className="flex-1 col">
            <Textbox label='Tn vai tr *'/>
            <Textbox label='M t:' type={InputType.field} />
            <div className="row">* L trng bt buc</div>
          </div>
          <div className="flex-1 col">
            <div className="row">Phn quyn chc nng *</div>
            <div className="box">
              <div className="row permission-group">
                Nhm chc nng A
              </div>
              {permisionGroup.map((role, index) => {
                return <div className="role" key={index}>
                  <input type="checkbox" />
                  <span>{role}</span>
                </div>
              })}
              <div className="row permission-group">
                Nhm chc nng B
              </div>
              {permisionGroup.map((role, index) => {
                return <div className="role" key={index}>
                  <input type="checkbox" />
                  <span>{role}</span>
                </div>
              })}
              
            </div>
          </div>
        </div>
        
      </div>

      <div className="app__layout-device__new-device__btn__bottom">
        <Button
          text='Hy b'
          isOutlined
          onClick={() => navigate('/dashboard/system/roles')}
        />
        <Button
          text='Thm vai tr'
          onClick={() => navigate('/dashboard/system/roles')}
        />
      </div>
    </div>
  );
};

export default NewRole;
