import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router';
import './sitemap.scss';
import { ReactComponent as ArrowIc } from '../../Assets/arrow.svg';
enum ServicePath {
  service = 'Dch v',
  list = 'Danh sch dch v',
  new = 'Thm dch v',
  modify = 'Cp nht',
  detail = 'Chi tit',
}

enum DevicePath {
  device = 'Thit b',
  list = 'Danh sch thit b',
  new = 'Thm thit b',
  modify = 'Cp nht thit b',
  detail = 'Chi tit thit b',
}

enum QueuePath {
  queue = "Cp s",
  list = "Danh sch cp s",
  new = "Cp s mi",
  detail = "Chi tit"
}

enum ReportPath {
  report = "Bo co",
  create = "Lp bo co"
}

enum SystemPath {
  system = "Ci t h thng",
  accounts = "Qun l ti khon",
  new_account = "Ti khon mi",
  new_role = "Thm vai tr",
  logs = "Nht k hot ng",
  modify_account = "Cp nht ti khon",
  roles = "Qun l vai tr",
}

const SiteMap: React.FC = () => {
  const location = useLocation();
  const [path, setPath] = useState<string[]>([]);
  useEffect(() => {
    const arr = location.pathname.split('/');
    setPath(arr);
  }, [location]);
  return (
    <div className="sitemap">
      {path.map((pathname: string, index) => {
        if (index > 1) {
          return (
            <div className="path">
              <div className="path__arrow">
                <ArrowIc />
              </div>
              <div className="path__name">
                {path[2] === 'service' &&
                  ServicePath[pathname as keyof typeof ServicePath]}
                {path[2] === 'device' &&
                  DevicePath[pathname as keyof typeof DevicePath]}
                {path[2] === 'queue' &&
                  QueuePath[pathname as keyof typeof QueuePath]}
                {path[2] === 'report' &&
                  ReportPath[pathname as keyof typeof ReportPath]}
                {path[2] === 'system' &&
                  SystemPath[pathname as keyof typeof SystemPath]}
                {path[2] === 'panel' && 'Dashboard'}
              </div>
            </div>
          );
        }
      })}
    </div>
  );
};

export default SiteMap;
